import {
  Box, Button, List, Stack, Typography,
} from '@mui/material';
import moment from 'moment';
import { useState } from 'react';
import { useSelector } from 'react-redux';
import { selectDate, selectSchedules } from '../../../utils/redux/schedule/scheduleSlice';
import ModalStaticBackdrop from '../../layouts/ModalStaticBackdrop';
import ScheduleCard from './ScheduleCard';

function ScheduleList() {
  const schedules = useSelector(selectSchedules);
  const date = moment(useSelector(selectDate)).format('YYYY-MM-DD');
  const [selectedSchedule, setSelectedSchedule] = useState(null);
  const [scheduleModalOpen, setScheduleModalOpen] = useState(false);

  return (
    <>
      {schedules.filter((el) => el.date === date).length === 0 && (
        <Stack
          justifyContent="center"
          alignItems="center"
        >
          <Box my={5}>
            <Typography>
              {date}
                 !
            </Typography>
          </Box>
        </Stack>
      )}
      <List
        sx={{
          bgcolor: 'background.paper',
          position: 'relative',
          overflow: 'auto',
          flex: 1,
          '& ul': { padding: 0 },
        }}
      >
        {
          schedules
            .filter((el) => el.date === date)
            .map((el) => (
              <ScheduleCard
                schedule={el}
                key={Math.random()}
                setScheduleModalOpen={setScheduleModalOpen}
                setSelectedSchedule={setSelectedSchedule}
              />
            ))
        }
      </List>
      <ModalStaticBackdrop
        keepMounted
        width="xl"
        open={scheduleModalOpen}
        component={(
          <Box p={1}>
            {
            selectedSchedule
            && (
              <>
                <Typography>
                  {selectedSchedule.event_name}
                </Typography>
                <Typography>
                  {selectedSchedule.date}
                </Typography>
                <Typography>
                  {selectedSchedule.start_time}
                </Typography>
                <Typography>
                  {selectedSchedule.end_time}
                </Typography>
                <Typography>
                  {JSON.stringify(selectedSchedule.categories)}
                </Typography>
                <Typography>
                  {selectedSchedule.type}
                </Typography>
                <Typography>
                  {selectedSchedule.expected_spending}
                </Typography>
                <Typography>
                  {selectedSchedule.importance}
                </Typography>
                <Typography>
                  {JSON.stringify(selectedSchedule.exclusion)}
                </Typography>

              </>
            )
            }
            <Button
              variant="contained"
              fullWidth
              onClick={() => setScheduleModalOpen(false)}
            >
              
            </Button>
          </Box>
        )}
      />
    </>
  );
}

export default ScheduleList;
